import * as React from 'react';
import {useEffect, useState} from "react";
import {jsloader} from "../../helpers/jsloader";
import {BackBtn} from "../../components/backBtn";
import {ROUTES} from "../../config";
import {Compare} from "./compare/compare";
import {Controls} from "./controls/controls";

import {PhotoMakeUp} from "./photoMakeUp";

export const BeautyPhoto = ({img}) => {
  const isBrowser = typeof window !== "undefined"
  const  [showCompare , setShowCompare]= useState(false);

  useEffect(() => {
    if (isBrowser && !window.beutyAlreadyLoaded) {
      window.makeUpAlpha= 0.5;
      window.beautyScale=1;
      window.lastChanges =   ()=>{};
      const jsArr = [
        // "/helpers/identify-ua.js",
        // "/helpers/camera-controller2.js" ,
        "/helpers/pre-vmt.js",
        "/vmt/vmt.js",
        "/helpers/post-vmt.js",
        "/helpers/vmt-helper.js"
      ]
      jsloader(jsArr);
      window.beutyAlreadyLoaded = true
    }
  }, [])

  if (!isBrowser) return <>  </>
  return (
    <div className={'fullscreen'}>
      <BackBtn external  link={ROUTES.homeBeauty}/>
      <Compare showCompare={showCompare}   />
      <Controls setShowCompare={setShowCompare}  typePhoto={true} showCompare={showCompare} />
      <PhotoMakeUp imgSrc={img}/>
    </div>
  );
};
