import * as React from 'react';
import {useEffect, useState} from "react";
import {Loader} from "../../components/loader/loader";

export const PhotoMakeUp = ({imgSrc}) => {

  const [loading , setLoading]= useState(true);

  useEffect(()=>{
   const img =  document.createElement('img');
   img.src=imgSrc;

    img.addEventListener('load',()=>{

      var base_canvas = document.getElementById("tryon-live-canvas-0");
      var base_ctx = base_canvas.getContext("2d");

      const height = window.innerHeight*0.6;
      const width  =  height/img.height*img.width
      base_canvas.width = width ;
      base_canvas.height = height ;
      base_ctx.drawImage(img, 0, 0 ,width ,height);

    })
    const start = () => {
      var success = window.VmtHelper.registerCanvases("tryon-live-canvas-0", "tryon-live-canvas-1");
      if (success) {
        window.VmtHelper.cycleForSingleImage();
        window.VmtHelper.setLive(false);
        setLoading(false);
      }
    }

    document.addEventListener("vmt_is_ready",start );
    return ()=>{ document.removeEventListener("vmt_is_ready",start );}
  },[])

  return <div>{  <Loader  showIt={loading}/>} </div>
};
