import * as React from "react"
import {Layout} from "../components/Layout";
import {BeautyPhoto} from "../screens/beauty/beautyPhoto";




const BeautyPage = ({location}) => {

  const img = location.search.replace('?','');
  return (
    <Layout>
      <BeautyPhoto img={img}/>
    </Layout>
  )
}

export default BeautyPage
